<template>
  <div class="introduce-wrapper" ref="introduce">
    <Title :titleImg="img" englistTitle="Product introduction" />
    <div class="model-list" v-for="item in modelList" :key="item.index">
      <Model 
      :mainTitle="item.mainTitle" 
      :explain="item.explain"
      :num="item.index"
      :img="item.img"
      :hover="item.hover"
      :circleColor="item.circleColor"
      :isMobile="isMobile"
      :mobileBg="item.hover"
      :mobileCircle="item.circleColor"
      />
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import Title from '@/components/title/title.vue'
import Model from '@/components/model/model.vue'
export default {
  components:{
    Title,Model,Footer
  },
  data(){
    return{
      isFixed:false,
      isMobile:false,
      img:require('../assets/images/introduce-title.png'),
      modelList:[{
        mainTitle:'3D模型',
        explain:'鞋款在小程序上直接用3D展示，按住鞋子即可进行360度旋转，能清楚的看到鞋子整体外观，比主图展现更直截了当，长按鞋子任何一个部位，会出现部位图及相应简短说明。点击3D空间即可体验更有趣的购鞋交互体验。3d模型在移动端展示是未来企业为客户提供物品展示最好的方式，这对企业在未来市场竞争中至关重要。',
        index:"01",
        img:require('../assets/images/3d.png'),
        hover:'#09D293',
        circleColor:'#1A09D5'
      },{
        mainTitle:'AR展陈',
        explain:'ar技术给现实加了一层虚拟维度，不会完全把体验者隔绝在虚拟环境里。通过摄像头，ar采用全息投影技术在周围任意一处都可以看到鞋款的虚拟影像，让用户同时与虚拟和现实的层面进行互动。这项技术在日常应用场景里会有巨大优势，它赋予了用户更大的主动权来个性化增强的信息。',
        index:"02",
        img:require('../assets/images/show.png'),
        hover:'#0DC7D8',
        circleColor:'#2AFFEA'
      },{
        mainTitle:'AR试穿',
        explain:'通过ar增强现实技术与智能手机的结合，运用小程序帮助你看到喜欢的鞋款“试穿”上脚的效果，可以模拟出鞋子在光线不同、场景不同下的各种‘试穿’效果，以及鞋子和整体穿着是否搭配。更是增强了用户网上购鞋的体验，解决了用户收到鞋发现不适合而退货的问题。',
        index:"03",
        img:require('../assets/images/try.png'),
        hover:'#1866DB',
        circleColor:'#43FFC4'
      }]
    }
  },
  mounted() {
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    
    if(w <= 768){
      height= this.$refs.introduce.offsetHeight + 95;
    }else{
      height= this.$refs.introduce.offsetHeight + 142;
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }

    this.isMobile = w <= 768
  }
}
</script>

<style lang="scss" scoped>
@media (max-width: 768px){
  .introduce-wrapper{
    .model-list{
      margin-bottom:37px;
    }
  }
}
@media (min-width: 768px){
  .introduce-wrapper{
    .model-list{
      width: 1411px;
      margin:42px auto 0 auto;
    }
  }
}
</style>