<template>
  <div class="model-wrapper" @mouseenter="enter" @mouseleave="leave" :style="{background: !isMobile ? bgColor : mobileBg}">
      <div class="model">
        <h3><span class="round"></span>{{mainTitle}}</h3>
        <p>{{explain}}</p>
      </div>
      <div class="shoes-box">
        <img :src="img" :alt="mainTitle">
      </div>
      <div class="num-box">
        <div class="num-round" :style="{borderColor:!isMobile ? circle : mobileCircle}"></div>
        <span>{{num}}</span>
      </div>
  </div>
</template>

<script>
export default {
  props:{
    mainTitle:String,
    explain:String,
    img:String,
    num:String,
    circleColor:String,
    hover:String,
    isMobile:Boolean,
    mobileBg:String,
    mobileCircle:String
  },
  data(){
    return {
      bgColor:'',
      circle:''
    }
  },
  methods:{
    enter(){
      this.bgColor = this.hover
      this.circle = this.circleColor
    },
    leave(){
      this.bgColor = ''
      this.circle = ''
    }
  }
}
</script>

