<template>
  <div class="partner-wrapper" ref="partner">
    <div class="partner-title">
      <Title :titleImg="img" englistTitle="Cooperative partner" isPartner/>
    </div>
    <div class="content">
      <div class="left-box"></div>
      <div class="scroll" ref="box" v-if="!isMobile">
        <img ref="img" src="../assets/images/partner.png"/>
        <img src="../assets/images/partner.png"/>
      </div>
      <div class="scroll" v-if="isMobile">
        <img src="../assets/images/mobile-partner.png"/>
      </div>
      <div class="right-box"></div>
    </div>
     <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import Title from '@/components/title/title.vue'
export default {
  components:{
    Title,Footer
  },
  data(){
    return{
      isFixed:false,
      isMobile:false,
      img:require('../assets/images/partner-title.png'),
    }
  },
  mounted(){
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    if(w > 768){
      this.toScroll()
      this.isMobile = false
    }else{
      this.isMobile = true
    }
    this.onFixed()
  },
  methods:{
    toScroll(){
      const target = this.$refs.box; 
      const img = this.$refs.img; 
      let initLeft = 0;
      target.style = `width: ${img.offsetWidth * 2}px `;   
      setInterval(function(){
          initLeft ++;
          if(initLeft >= img.offsetWidth ){
              initLeft = 0;
          }
          target.style = 'transform: translateX(-'+ initLeft +'px)';                
      },10)
    },
    onFixed(){
      const h = document.documentElement.clientHeight || document.body.clientHeight;
      const w = document.documentElement.clientWidth || document.body.clientWidth;
      let height = 0;
      if(w <= 768){
        height= this.$refs.partner.offsetHeight + 95;
      }else{
        height= this.$refs.partner.offsetHeight + 142;
      }

      if(height > h){
        this.isFixed = false
      }else{
        this.isFixed = true
      }
    }
  }
}
</script>

