<template>
  <div class="about-wrapper" ref="About">
    <Title :titleImg="img" englistTitle="About zugo" />
    <div class="content">
      <div class="zg-logo">
        <img src="../assets/images/about-logo.png"/>
      </div>
      <div class="zg-container">
        <h2>About zugo</h2>
        <h3>关于足购</h3>
        <p>作为全球领先的移动增强现实交互公司，“足购科技”拉近传统鞋业与数字媒体之间的距离。
使用“足购”的增强现实交互功能，用户可以通过他们的智能手机或电脑享受神奇的交互体验。这是一种革命性的营销方式！</p>
      </div>
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import Title from '@/components/title/title.vue'
export default {
  components:{
    Title,Footer
  },
  data(){
    return{
      isFixed:false,
      img:require('../assets/images/about-title.png'),
    }
  },
  mounted() {
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    if(w <= 768){
      height= this.$refs.About.offsetHeight + 95;
    }else{
      height= this.$refs.About.offsetHeight + 142;
    }
    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }
  }
}
</script>

