export default {
  components:{
    Footer
  },
  data(){
    return{
      isFixed:false,
      isMobile:false
    }
  },
  mounted() {
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    if(w <= 768){
      height= this.$refs.contact.offsetHeight + 95;
      this.isMobile = true
    }else{
      height= this.$refs.contact.offsetHeight + 142;
      this.isMobile = false
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }
  }
}
</script>

