<template>
  <div class="home" ref="home">
    <div class="light"></div>
    <div class="wrapper">
      <div class="shoes">
         <canvas ref="comCanvas"></canvas>
      </div>
      <div class="main-title"><img src="../assets/images/main-title.png"/></div>
      <div class="sub-title"><img src="../assets/images/sub-title.png"/></div>
      <div class="art-title"><img src="../assets/images/art-title.png"/></div>
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import * as THREE from 'three';
import { EquirectangularToCubeGenerator } from 'three/examples/jsm/loaders/EquirectangularToCubeGenerator.js';
import { PMREMGenerator } from 'three/examples/jsm/pmrem/PMREMGenerator.js';
import { PMREMCubeUVPacker } from 'three/examples/jsm/pmrem/PMREMCubeUVPacker.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { OutlinePass } from 'three/examples/jsm/postprocessing/OutlinePass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
import { FXAAShader } from 'three/examples/jsm/shaders/FXAAShader.js';
import { BleachBypassShader } from 'three/examples/jsm/shaders/BleachBypassShader.js';
import { ColladaLoader } from 'three/examples/jsm/loaders/ColladaLoader';

export default {
  components:{
    Footer
  },
  data: function() {
    return {
      percent:'100%',
      isFixed:false,
      scene: null,
      camera: null,
      renderer: null,
      mat: null,
      geo: null,
      composer: null,
      outlinePass: null
    };
  },
  mounted: function() {
    let comCanvas = this.$refs.comCanvas;
    // 渲染完执行的函数
    /**
     * 创建场景对象Scene
     */
    let scene = new THREE.Scene();
    this.scene = scene;

    /**
     * 光源设置
     */
    // 环境光
    let ambient = new THREE.AmbientLight(0xffffff);
    ambient.intensity = 1;
    // this.scene.add(ambient);
    let direction = new THREE.DirectionalLight(0x555555);
    direction.position.set(1.35, 0.93, 0.17);
    this.scene.add(direction);
    // 颜色、光照强度（默认为1）、光源到光照强度为0的位置(默认为0)、沿着光照距离的衰减量（默认值为1）
    let point1 = new THREE.PointLight(0x808080, 1, 0.2, 0.5);
    point1.position.set(0.03, -0.02, 0.18);
    this.scene.add(point1);
    let point2 = new THREE.PointLight(0xaaaaaa, 1, 0.3);
    point2.position.set(-0.04, 0.07, -0.26);
    this.scene.add(point2);
    var canvasW = 850;
    var canvasH = 396;
    /**
     * 相机设置:透视投影
     */
    this.camera = new THREE.PerspectiveCamera(45, canvasW / canvasH, 0.1, 10000);
    this.camera.position.set(0, 0, 0.8);
    this.camera.lookAt(this.scene.position);
    /**
     * 创建渲染器对象
     */
    this.renderer = new THREE.WebGLRenderer({canvas: comCanvas, antialias: true,alpha:true});
    this.renderer.setSize(canvasW , canvasH);
    this.renderer.setClearColor(0xff0000, 0);// 设置背景颜色
 
    this.renderer.gammaInput = true;// 输出时打开gamma
    this.renderer.gammaOutput = true;// 输出时打开gamma

    // 效果组合器，是一个可以将结果输出到屏幕上的通道
    this.composer = new EffectComposer(this.renderer);
    // 原始场景渲染结果,会渲染场景，但不会将渲染结果输出到屏幕上
    let renderPass = new RenderPass(this.scene, this.camera);
    this.composer.addPass(renderPass);
    this.outlinePass = new OutlinePass(new THREE.Vector2(canvasW / canvasH), this.scene, this.camera);
    this.composer.addPass(this.outlinePass);
    // FXAAShader是添加抗锯齿锯齿的效果
    let effectFXAA = new ShaderPass(FXAAShader);
    effectFXAA.uniforms['resolution'].value.set(1 /canvasW, 1 /canvasH);
    effectFXAA.renderToScreen = true;
    this.composer.addPass(effectFXAA);
    // bleachBypassShader是添加镀银的效果
    let bleachBypassShader = new ShaderPass(BleachBypassShader);
    bleachBypassShader.renderToScreen = true;
    this.composer.addPass(bleachBypassShader);

    let mesh;
    let textureLoader = new THREE.TextureLoader();

    let loader = new ColladaLoader();

    loader.load('/static/themeshoe.DAE', (result) => {
      this.geo = result.scene.children[1].geometry.clone();

      textureLoader.load('/static/env1.jpg', (res) => {
        res.encoding = THREE.sRGBEncoding;
        let cubemapGenerator = new EquirectangularToCubeGenerator(res, {resolution: 512});
        let cubeMapTexture = cubemapGenerator.update(this.renderer);
        let pmremGenerator = new PMREMGenerator(cubeMapTexture);
        pmremGenerator.update(this.renderer);
        let pmremCubeUVPacker = new PMREMCubeUVPacker(pmremGenerator.cubeLods);
        pmremCubeUVPacker.update(this.renderer);
        let cube = pmremCubeUVPacker.CubeUVRenderTarget;
        res.dispose();
        pmremGenerator.dispose();
        pmremCubeUVPacker.dispose();
        textureLoader.load('/static/map1.jpg', (mapres) => {
          this.mat = new THREE.MeshPhysicalMaterial({
            map: mapres,
            envMap: cube.texture,
            needsUpdate: true,
            roughness: 0.39,
            metalness: 1.0,
            clearCoat: 1.0,
            clearCoatRoughness: 1.0,
            reflectivity: 1.0,
            envMapIntensity: 0.65
          });
          mesh = new THREE.Mesh(
            this.geo,
            this.mat
          );
          this.outlinePass.selectedObjects = [mesh];
          this.outlinePass.edgeStrength = 2;// 强度
          this.outlinePass.edgeGlow = 0.3;// 强度
          this.outlinePass.edgeThickness = 4;// 边缘浓度
          this.outlinePass.pulsePeriod = 5;// 闪烁频率，值越大，频率越低
          this.outlinePass.visibleEdgeColor.set(0x555555);
          mesh.rotation.x = -Math.PI / 2;
          mesh.position.set(0, -0.16, 0);
          mesh.scale.set(4,4,4);
          mesh.name = 'root';
          this.scene.add(mesh);
        });
      });
    });
      comCanvas.style.width = '120%'
      comCanvas.style.height = '120%'
    this.render();

    // iiii
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    if(w <= 768){
      height= this.$refs.home.offsetHeight + 95;
    }else{
      height= this.$refs.home.offsetHeight;
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }
  },
  methods: {
    render() {
      this.composer.render();
      this.scene.traverse((e) => {
        if (e.name === 'root') {
          e.rotation.z += 0.01;
        }
      });
      requestAnimationFrame(this.render); // 请求再次执行渲染函数render，渲染下一帧
    }
  }
};
</script>

<style lang="scss" scoped>

@media (max-width: 768px){
  .home{
    position: relative;
    height: auto;
  }
  .wrapper{
    margin-top:52px;
    padding-bottom:20px;
    .shoes{
      width:100%;
      height: 184px;
      canvas{
        margin-left:-10%;
      }
    }
    .main-title{
        width:80%;
        height: auto;
        margin:30px auto 0 auto;
      }
      .sub-title{
        width:90%;
        height:auto;
        margin:10px auto 0 auto;
      }
      .art-title{
        width:60%;
        height:auto;
        margin:4px auto 0 auto;
      }
      img{
        width: 100%;
      }
  }
}

@media (min-width: 768px){
  .home{
    position: relative;
    top:0;
    bottom:0;
    left:0;
    right:0;
    height: 100vh;
    margin-top:-142px;
    }
    .light{
      position: absolute;
      top:0;
      right:0;
      left:0;
      right:0;
      min-height: 100vh;
      background: url('../assets/images/bg.png') no-repeat;
      z-index:7;
    }
    .wrapper{
      position:absolute;
      top:15vh;
      left:50%;
      width: 850px;
      margin:0 auto 0 -415px;
      z-index: 99;
      .shoes{
        width:100%;
        height:54vh;
        margin:-40px auto 0 auto;
        canvas{
          width: 100%;
          height: 100%;
          margin-left:-10%;
          margin-top:-5%;
        }
      }
      .main-title{
        width:60%;
        height: auto;
        margin:0 auto;
        animation:translateText 1s;
      }
      .sub-title{
        width:60%;
        height:auto;
        margin:7px auto 0 auto;
        animation:translateText 1.4s;
      }
      .art-title{
        width:40%;
        height:auto;
        margin:8px auto 0 auto;
        animation:translateText 1.8s;
      }
      img{
        width: 100%;
      }
    } 
  }
  @keyframes translateText{
    from{
      transform: translate(0,900px);
    }
    to{
      transform: translate(0,0);
    }
  }



</style>
